@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Mono:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");

:root {
    --black: #101010;
    --code-black: #1e1e1e;
    --light-black: #282828;

    --white: #f0f0f0;
    --light-gray: #cccccc;
    --gray: #b7b7b7;

    --accent: #7c8cff;

    --maxw: 1550px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
li {
    color: var(--white);
    margin: 0;
}

p,
li,
.body {
    color: var(--white);
    font-family: "DM Sans";
    font-size: clamp(18px, 1.25vw, 22px);
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.36px;
}

p.accent,
span.accent {
    color: var(--accent);
}

ul {
    margin: 0;
    /* padding: 0; */
    padding-left: 25px;

    li {
        margin-bottom: 5px;
    }
}

@media all and (max-width: 800px) {
    p,
    li,
    .body {
        font-size: clamp(16px, 4.267vw, 18px);
        letter-spacing: 0.32px;
    }
}

.code,
.code p {
    color: var(--white);
    font-family: "DM Mono", monospace;
    font-size: clamp(14px, 0.972vw, 18px);
    font-weight: 400;
    line-height: 120%; /* 16.8px */
    letter-spacing: 0.28px;
}

/* /////////////////// */
/* Header Styles */
/* /////////////////// */

/* //////// */
/* Desktop */
/* //////// */

.xl_header,
h1 {
    font-family: "Black Mango";
    font-size: clamp(75px, 5.625vw, 95px);
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    letter-spacing: 6.8px;
}

.l_header,
h2,
h3 {
    font-family: "Black Mango";
    font-size: clamp(45px, 3.333vw, 55px);
    font-style: normal;
    font-weight: 600;
    line-height: 110%;
    letter-spacing: 1.92px;
}

.m_header,
h4 {
    font-family: "Black Mango";
    font-size: clamp(60px, 4.444vw, 70px);
    font-style: normal;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 1.28px;
}

.s_header,
h5 {
    font-family: "Black Mango";
    font-size: clamp(45px, 3.333vw, 55px);
    font-style: normal;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0.96px;
}

.xs_header,
h6 {
    font-family: "Black Mango";
    font-size: clamp(35px, 2.5vw, 45px);
    font-style: normal;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0.72px;
}

.subtext {
    font-family: "Ostrich Sans";
    font-size: clamp(40px, 2.778vw, 45px);
    font-style: normal;
    font-weight: 900;
    line-height: 120%;
    letter-spacing: 18px;
    text-transform: uppercase;
}

.eyebrow {
    font-family: "Ostrich Sans";
    font-size: clamp(20px, 1.667vw, 30px);
    font-style: normal;
    font-weight: 900;
    line-height: 120%;
    letter-spacing: 3.36px;
    text-transform: uppercase;
}

